body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.book-list {    
    height: 500px;
    overflow-y: scroll;
    text-align: center;
    margin: 10px;
    padding: 10px;
}
.App{
    text-align: center;
    width: 90%;
    margin: 10px;
    padding: 10px;
}
.summary-book{
    background-color: lightblue;
    border: 1px solid black;
    text-align: left;
}

.book-detail{
    background-color: lightblue;
    border: 1px solid black;
    text-align: left;
    margin: 10px;
    padding: 10px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}
.loading{
    position: absolute;
    margin: auto;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100px;
    height: 100px;
}


